import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { ActivatedRoute } from '@angular/router';
import { UtilService } from 'src/app/services/util.service';
import { RequestService } from 'src/app/api/request.service';
import { HtmlToImageService } from 'src/app/services/html-to-image.service';
import { environment } from 'src/environments/environment';
import { SharerService } from 'src/app/services/sharer.service';
import { ScreenLoadingService } from 'src/app/services/screen-loading.service';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-export',
  templateUrl: './export.page.html',
  styleUrls: ['./export.page.scss'],
})
export class ExportPage implements OnInit {

  //lucas
  public id : any;
  public gameModel : Model;
  public game : any ;
  public inServer = false;

  constructor( 
    public navCtr : NavController,   
    public route : ActivatedRoute,
    public requesService : RequestService,
    public util : UtilService,
    public htmlToImageService : HtmlToImageService,
    public shareService : SharerService,
    public loadingService : ScreenLoadingService
    
    )
    {
    this.id = this.route.snapshot.paramMap.get('id');
    this.gameModel = new Model('Game',requesService);

   }

  ngOnInit() {
    this.init();
  }


  async init(event = null){

    this.loadingService.start();

    this.gameModel.api_functionModel(this.id,'dataProfile').subscribe(async (data) => {
      if(data['status'] == 'success')
        this.game = data['data'];
      console.log('Game',this.game);
      await this.util.delay(100);
      
      await this.htmlToImageService.run('export');

      //this.game = null;

      //await this.util.delay(200);

      this.uploadImage();



      if(event)
        event.target.complete();
      //this.dialogService.dimissLoaging();
    },
    error =>{
      this.loadingService.end();
      //this.dialogService.dimissLoaging(); 
    })
  }

  export(){
    this.htmlToImageService.dowload(this.game.id);
  }

  uploadImage(){

    var data = {
      data : this.htmlToImageService.img,
      url : '/results/',
      thumb : false,
    }

    this.gameModel.api_functionModel(this.id,'addPreview',data).subscribe(
      response => {
        console.log(response);

        this.inServer = true;

       

        //this.navCtr.back();

        this.loadingService.end();

        //console.log(this.htmlToImageService);

      },error =>{
        console.error(error);
        this.loadingService.end();
      }
    );
  
  }

  facebook(){
    let url = `https://www.facebook.com/sharer?u=${environment.server_url_base}/shareResultF/${this.id}?${new Date().valueOf()}`;

    console.log('url',url);

    //alert('a');
    //window.location.href = url;
    this.shareService.goTo(url)
  }


}
